/**
 * Subscription Page
 *
 * ✅ CODE-003: Migrated to TypeScript
 */

import React, { useEffect, useState, type FC } from 'react';
import { Check, Zap, Crown, Shield, Loader2 } from 'lucide-react';
import { apiGet, apiPost } from '../utils/api';
import { logger } from '../utils/logger';
import { toast } from 'sonner';

// ===========================
// Types
// ===========================

type PlanTier = 'free' | 'basic' | 'professional' | 'enterprise';

interface Plan {
  id: PlanTier;
  name: string;
  price: string;
  period?: string;
  features: string[];
  limit: string;
  color: string;
  popular?: boolean;
  minSeats?: number;
}

interface CurrentPlan {
  tier: PlanTier;
  name: string;
}

interface UsageData {
  cases_used: number;
  cases_limit: number;
  tokens_used: number;
  tokens_limit: number;
}

interface SubscriptionResponse {
  plan?: CurrentPlan;
  tier?: PlanTier;
  usage?: UsageData;
  monthly_cases?: {
    used?: number;
    limit?: number;
  };
  monthly_tokens?: {
    used?: number;
    limit?: number;
  };
}

// ===========================
// Constants
// ===========================

const PLANS: Plan[] = [
  {
    id: 'free',
    name: 'Free',
    price: '₪0',
    features: ['20 עמודים בחודש', 'ניתוח בסיסי (Tesseract OCR)', 'ללא שמירת קבצים', 'תיק אחד בחודש'],
    limit: '50K טוקנים',
    color: 'bg-gray-700'
  },
  {
    id: 'basic',
    name: 'Basic',
    price: '₪99',
    period: '/חודש',
    features: ['250 עמודים בחודש', 'Google Vision OCR', '2 תיקים בחודש', 'ייצוא ל-Word', 'שמירה 90 יום'],
    limit: '200K טוקנים',
    color: 'bg-blue-600'
  },
  {
    id: 'professional',
    name: 'Professional',
    price: '₪199',
    period: '/חודש',
    features: ['1,000 עמודים בחודש', 'כל פיצ\'רי Basic +', 'ניתוח Intelligence', 'ייצוא ל-Word + Excel', 'API מוגבל', '5 תיקים בחודש'],
    limit: '500K טוקנים',
    popular: true,
    color: 'bg-purple-600'
  },
  {
    id: 'enterprise',
    name: 'Enterprise',
    price: '₪999',
    period: '/מושב/חודש',
    features: ['10,000 עמודים/מושב', 'ניהול צוות', 'תיקים משותפים', 'API מלא + Webhooks', 'SSO/SAML', 'מנהל לקוח ייעודי'],
    limit: '5M טוקנים/מושב',
    color: 'bg-gradient-to-r from-amber-500 to-orange-600',
    minSeats: 2
  }
];

// ===========================
// Component
// ===========================

const SubscriptionPage: FC = () => {
  const [currentPlan, setCurrentPlan] = useState<CurrentPlan | null>(null);
  const [usage, setUsage] = useState<UsageData | null>(null);
  const [loading, setLoading] = useState(true);
  const [processing, setProcessing] = useState(false);

  useEffect(() => {
    loadSubscriptionData();
  }, []);

  const loadSubscriptionData = async (): Promise<void> => {
    try {
      const data = await apiGet('/api/subscription/me') as SubscriptionResponse;
      // Handle both API response formats
      setCurrentPlan(data.plan || {
        tier: data.tier || 'free',
        name: (data.tier || 'free').charAt(0).toUpperCase() + (data.tier || 'free').slice(1)
      });
      setUsage(data.usage || {
        cases_used: data.monthly_cases?.used || 0,
        cases_limit: data.monthly_cases?.limit || 5,
        tokens_used: data.monthly_tokens?.used || 0,
        tokens_limit: data.monthly_tokens?.limit || 50000
      });
    } catch (error) {
      logger.error('Failed to load subscription:', error);
      // Fallback mock data if API fails (for dev)
      setCurrentPlan({ tier: 'free', name: 'Free' });
      setUsage({ cases_used: 0, cases_limit: 5, tokens_used: 0, tokens_limit: 50000 });
    } finally {
      setLoading(false);
    }
  };

  const handleUpgrade = async (tier: PlanTier): Promise<void> => {
    setProcessing(true);
    try {
      // In a real app, this would redirect to Stripe or open a payment modal
      await apiPost('/api/subscription/upgrade', { tier });
      toast.success(`שודרגת בהצלחה לתוכנית ${tier}!`);
      loadSubscriptionData();
    } catch (error) {
      toast.error('שדרוג נכשל. נסה שוב מאוחר יותר.');
    } finally {
      setProcessing(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Loader2 className="w-8 h-8 animate-spin text-blue-500" />
      </div>
    );
  }

  return (
    <div className="p-8 max-w-6xl mx-auto text-white">
      <h1 className="text-4xl font-bold mb-2">ניהול מנוי</h1>
      <p className="text-gray-400 mb-8">צפה בתוכנית הנוכחית שלך ושדרג לפי הצורך</p>

      {/* Current Usage Stats */}
      {usage && (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
          <div className="bg-white/5 border border-white/10 rounded-2xl p-6">
            <div className="flex justify-between items-start mb-4">
              <div>
                <p className="text-gray-400 text-sm">תיקים בשימוש החודש</p>
                <h3 className="text-2xl font-bold">{usage.cases_used} / {usage.cases_limit === -1 ? '∞' : usage.cases_limit}</h3>
              </div>
              <div className="p-2 bg-blue-500/20 rounded-lg">
                <Shield className="text-blue-400 w-6 h-6" />
              </div>
            </div>
            <div className="w-full bg-gray-700 h-2 rounded-full overflow-hidden">
              <div
                className="bg-blue-500 h-full"
                style={{ width: `${Math.min(100, (usage.cases_used / (usage.cases_limit || 1)) * 100)}%` }}
              />
            </div>
          </div>

          <div className="bg-white/5 border border-white/10 rounded-2xl p-6">
            <div className="flex justify-between items-start mb-4">
              <div>
                <p className="text-gray-400 text-sm">טוקנים (AI Usage)</p>
                <h3 className="text-2xl font-bold">
                  {(usage.tokens_used / 1000).toFixed(1)}k / {usage.tokens_limit === -1 ? <span className="text-emerald-400">∞</span> : `${(usage.tokens_limit / 1000).toFixed(0)}k`}
                </h3>
              </div>
              <div className="p-2 bg-purple-500/20 rounded-lg">
                <Zap className="text-purple-400 w-6 h-6" />
              </div>
            </div>
            {usage.tokens_limit !== -1 && (
              <div className="w-full bg-gray-700 h-2 rounded-full overflow-hidden">
                <div
                  className="bg-purple-500 h-full"
                  style={{ width: `${Math.min(100, (usage.tokens_used / (usage.tokens_limit || 1)) * 100)}%` }}
                />
              </div>
            )}
          </div>

          <div className="bg-white/5 border border-white/10 rounded-2xl p-6 flex items-center justify-between">
            <div>
              <p className="text-gray-400 text-sm">התוכנית הנוכחית</p>
              <h3 className="text-2xl font-bold capitalize text-green-400">{currentPlan?.tier || 'Free'}</h3>
              <p className="text-xs text-gray-500 mt-1">פעיל עד: מתחדש חודשית</p>
            </div>
            <div className="p-3 bg-green-500/20 rounded-full border border-green-500/30">
              <Crown className="text-green-400 w-8 h-8" />
            </div>
          </div>
        </div>
      )}

      {/* Plans Comparison */}
      <h2 className="text-2xl font-bold mb-6">שדרג תוכנית</h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {PLANS.map((plan) => {
          const isCurrent = currentPlan?.tier === plan.id;
          return (
            <div
              key={plan.id}
              className={`relative rounded-2xl p-8 border transition-all duration-300 ${
                isCurrent
                  ? 'bg-white/10 border-green-500 shadow-lg shadow-green-900/20'
                  : 'bg-white/5 border-white/10 hover:border-blue-500/50 hover:bg-white/10'
              } ${plan.popular ? 'md:-mt-4 md:mb-4 border-blue-500/50' : ''}`}
            >
              {plan.popular && !isCurrent && (
                <div className="absolute top-0 right-1/2 translate-x-1/2 -translate-y-1/2 bg-blue-500 text-xs font-bold px-3 py-1 rounded-full">
                  הכי פופולרי
                </div>
              )}
              {isCurrent && (
                <div className="absolute top-4 left-4 bg-green-500/20 text-green-400 text-xs font-bold px-3 py-1 rounded-full flex items-center gap-1">
                  <Check size={12} /> הנוכחי
                </div>
              )}

              <h3 className="text-xl font-bold mb-2">{plan.name}</h3>
              <div className="flex items-baseline mb-6">
                <span className="text-4xl font-bold">{plan.price}</span>
                {plan.period && <span className="text-gray-400 ml-1 text-sm">{plan.period}</span>}
              </div>

              <p className="text-sm text-gray-300 mb-6 border-b border-white/10 pb-4">
                מגבלה: {plan.limit}
              </p>

              <ul className="space-y-3 mb-8">
                {plan.features.map((feature, i) => (
                  <li key={i} className="flex items-start gap-3 text-sm text-gray-300">
                    <Check className="text-green-400 shrink-0" size={16} />
                    {feature}
                  </li>
                ))}
              </ul>

              <button
                disabled={isCurrent || processing}
                onClick={() => handleUpgrade(plan.id)}
                className={`w-full py-3 rounded-xl font-bold transition-all ${
                  isCurrent
                    ? 'bg-green-600/20 text-green-400 cursor-default'
                    : plan.id === 'enterprise'
                    ? 'bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700 text-white'
                    : 'bg-blue-600 hover:bg-blue-700 text-white'
                }`}
              >
                {isCurrent ? 'התוכנית הפעילה שלך' : processing ? 'מעבד...' : `בחר ${plan.name}`}
              </button>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default SubscriptionPage;
